import FullPageEmptyState from '../../../components/full-page-empty-state.vue'
export default {
	components: {
		"full-page-empty-state": FullPageEmptyState
	},
	data() {
		return {
			creditChangeTypes: {
				0: {
					text: "签到获得",
					icon: "assignment_turned_in"
				},
				1: {
					text: "购买商品奖励",
					icon: "add_shopping_cart"
				},
				2: {
					text: "购买时抵扣订单金额",
					icon: "redeem"
				}
			},
			creditHistory: [],
			credit: 0,
			creditDiff: 0,
			totalCheckInDays: 0,
			continuousCheckInDays: 0,
			// 浇水部分
			userInfo: {
				nickname: 'Pin',	//姓名
				gender: 1,			//姓别
				avatarUrl: ''	//头像
			},
			pluss: false,		//+1动画
			mupiao: 1,			//成长进度 小1，中2 ，大3
			movetree: true,		//树动画开关
			treemove: false,	//树大小类型
			wateroff: true, 	//浇水动画开关
			watercss: false,	//水壶动画开关
			waterdom: false		//水滴动画开关
		}
	},
	onLoad() {
		this.userInfo = uni.getStorageSync('userInfo')
	},
	onShow() {
		this.loadMyCreditsRecord()
	},
	methods: {
		doCheckIn() {
			let that = this
			this.$pin.request('GET', '/commons/user/check-in', null,
				successData => {
					if (successData.code == that.$pin.code.success) {
						that.loadMyCreditsRecord(true) // 签到并显示浇水动画
					} else {
						uni.showToast({
							icon: 'none',
							title: successData.message
						})
					}
				},
				failData => {
					uni.showToast({
						icon: 'none',
						title: '签到失败，请重试'
					})
				})
		},
		loadMyCreditsRecord(isWatering) {
			let that = this
			this.$pin.request('GET', '/commons/user/credit-record', null,
				successData => {
					if (successData.code == that.$pin.code.success) {
						for (let i = 0; i < successData.data.creditHistory.length; i++) {
							successData.data.creditHistory[i].createTime = that.$pin.getFriendlyTimeText(successData.data.creditHistory[i]
								.createTime)
						}
						that.creditHistory = successData.data.creditHistory
						that.creditDiff = successData.data.credit-that.credit
						that.credit = successData.data.credit
						that.totalCheckInDays = successData.data.totalCheckInDays
						that.continuousCheckInDays = successData.data.continuousCheckInDays
						if(isWatering) {
							that.water() // 显示浇水动画
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '加载签到信息失败'
						})
					}
				},
				failData => {
					uni.showToast({
						icon: 'none',
						title: '加载签到信息失败，请重试'
					})
				})
		},
		// 浇水动画部分：点击头像放大
		zoom(o) {
			uni.previewImage({
				'urls': [o]
			});	
		},
		
		//点击树的动画
		tree() {
			if(this.movetree) {
				this.treemove = true;
				this.movetree = false;
				setTimeout(() => {
					this.movetree = true;
					this.treemove = false;
				}, 1000);
			};
		},
		
		//点击浇水动画
		water() {
			if(this.wateroff) {
				this.watercss = true;
				this.wateroff = false;
									
				setTimeout(() => {
					this.waterdom = true;
				}, 1500);
				
				setTimeout(() => {
					this.pluss = true;
					this.treemove = true;
					this.movetree = false;
				}, 2000);
				
				setTimeout(() => {
					this.waterdom = false;
					this.movetree = true;
				}, 3500);
				
				setTimeout(() => {
					this.pluss = false;
					this.wateroff = true;
					this.watercss = false;
					this.treemove = false;
					if(100 <= this.credit && 300 > this.credit) {
						this.mupiao = 2;
					};
					if(300 <= this.credit) {
						this.mupiao = 3;
					};
				}, 4000);	
			};
		}
	}
}
